import React, { useState } from 'react';

interface AccordionProps {
  title: string;
  description: string;
  open?: boolean;
}

const Accordion: React.FC<AccordionProps> = ({ title, description, open }) => {
  const [isOpen, setIsOpen] = useState(open);
  return (
    <div
      style={{ border: `2px solid rgba(255, 255, 255, 0.2)` }}
      className="flex flex-col w-full px-6 py-4 rounded-xl"
      key={title}
    >
      <div
        className="hover:cursor-pointer flex justify-between"
        onClick={() => setIsOpen(!isOpen)}
        role="button"
        onKeyPress={() => setIsOpen(!isOpen)}
        tabIndex={0}
      >
        <span className="text-lg">{title}</span>
        <img
          className="transition-all duration-1000 ease-in-out w-8 h-8"
          style={{ transform: `rotate(${isOpen ? 360 + 180 : 0}deg)` }}
          src="/images/arrowDown.png"
          alt=""
        />
      </div>
      <p
        className={`${
          isOpen ? `h-auto mt-4 opacity-100` : `h-0 mt-0 opacity-50`
        } text-base overflow-hidden transition-all duration-700 ease-in-out`}
      >
        {description}
      </p>
    </div>
  );
};

export default Accordion;
