import React from 'react';
import * as styles from './aboutContent.module.css';

const LINE_COLORS = [
  `#270FFF `,
  `rgb(20 219 60 / 38%)`,
  `rgb(250 30 14 / 35%)`,
  `#48cbd9`,
];
const AboutContent: React.FC<{ text: string; id: number }> = ({ text, id }) => (
  <div>
    <div
      style={{
        background: `linear-gradient(270deg,${LINE_COLORS[id]},rgba(255, 255, 255, 0) 100%)`,
      }}
      className={styles.line}
    />
    <p className={styles.text}>{text}</p>
  </div>
);

export default AboutContent;
